import React, { useState } from "react";
import PropTypes from "prop-types";
import { useForm } from "react-hook-form";
import { userLogin, userLogout } from "api/auth-api";
import { modifyPassword } from "api/user-api";
import { Button, Avatar, Text } from "components";
import { DEFAULT_PROFILE_IMAGE } from "api/url";
import { useNavigate } from "react-router-dom";
import { useSetRecoilState } from "recoil";
import { logoutProcess } from "recoil/authentication";
import * as S from "./ModifyUserInfo.style";

const propTypes = {
  token: PropTypes.string.isRequired,
  user: PropTypes.instanceOf(Object).isRequired,
  onClose: PropTypes.func,
};
const defaultProps = {
  onClose: () => {},
};

const ModifyPassword = ({ token, user, onClose }) => {
  const navigate = useNavigate();
  const setLogout = useSetRecoilState(logoutProcess);
  const [serverError, setServerError] = useState("");
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
  } = useForm({
    mode: "onSubmit",
    defaultValues: {
      currentPassword: "",
      newPassword: "",
      passwordConfirm: "",
    },
  });

  const onSubmit = async (data) => {
    const { currentPassword, newPassword } = data;

    try {
      setServerError("");
      const { email } = user;
      // 1.      
      const result = await userLogin(email, currentPassword);
      if (result) {
        // 2.     
        await modifyPassword(token, newPassword);
        // 3.     
        await userLogout();
        await setLogout();
        onClose();
        // 4.    ,  
        navigate("/?needLogin=true");
      }
    } catch (exception) {
      console.log(exception);
      setServerError(
        exception.response ? exception.response.data : " ."
      );
    }
  };

  return (
    <S.Form onSubmit={handleSubmit(onSubmit)}>
      <S.ProfileWrap>
        <Avatar src={user.image || DEFAULT_PROFILE_IMAGE} size={150} />
        <Text size="$b3" strong style={{ marginTop: 20 }}>
          {user.fullName || ""}
        </Text>
      </S.ProfileWrap>
      <S.ModalInputWrap>
        <S.ModalInput
          {...register("currentPassword", {
            required: { value: true, message: "  !" },
          })}
          useIcon={false}
          placeholder="  ."
          width="100%"
          type="password"
        />
        {errors.currentPassword && (
          <S.ErrorMsg>{errors.currentPassword.message}</S.ErrorMsg>
        )}
      </S.ModalInputWrap>
      <S.ModalInputWrap>
        <S.ModalInput
          {...register("newPassword", {
            required: {
              value: true,
              message: "  !",
            },
            minLength: {
              value: 8,
              message: " 8   !",
            },
            maxLength: {
              value: 12,
              message: " 12   !",
            },
            pattern: {
              value:
                /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]/,
              message: " , ,   ",
            },
          })}
          useIcon={false}
          placeholder="  ."
          width="100%"
          type="password"
        />
        {errors.newPassword && (
          <S.ErrorMsg>{errors.newPassword.message}</S.ErrorMsg>
        )}
      </S.ModalInputWrap>
      <S.ModalInputWrap>
        <S.ModalInput
          {...register("passwordConfirm", {
            required: {
              value: true,
              message: "  .",
            },
            validate: (value) =>
              value === watch("newPassword") || "  .",
          })}
          useIcon={false}
          placeholder="   ."
          width="100%"
          type="password"
        />
        {errors.passwordConfirm && (
          <S.ErrorMsg>{errors.passwordConfirm.message}</S.ErrorMsg>
        )}
      </S.ModalInputWrap>
      <S.ErrorMsg>{serverError}</S.ErrorMsg>
      <div>
        <Button
          type="submit"
          width={90}
          height={45}
          style={{ display: "inline-block", float: "right", marginLeft: 10 }}
        >
          
        </Button>
        <Button
          type="button"
          width={90}
          height={45}
          color="$main"
          backgroundColor="$bg_white"
          border
          onClick={onClose}
          style={{ display: "inline-block", float: "right" }}
        >
          
        </Button>
      </div>
    </S.Form>
  );
};

ModifyPassword.propTypes = propTypes;
ModifyPassword.defaultProps = defaultProps;

export default ModifyPassword;
